import React from "react";
import  {Avatar, Input, Modal, DatePicker, Form, Radio} from "antd";
import { WomanOutlined,ManOutlined } from '@ant-design/icons';
import moment from "moment";

import IntlMessages from "util/IntlMessages";
const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;
const formItemLayout = {
  labelCol: {
    xs: { span: 24 },
    sm: { span: 8 },
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 16 },
  },
};
const dateFormat = 'DD.MM.YYYY';

class AddContact extends React.Component {
  constructor(props) {
    super(props);

    const {id, thumb, firstname, lastname, birthday, gender, email, phone, selected, starred, frequently} = props.contact;
    this.state = {
      id,
      thumb,
      firstname,
      lastname,
      email,
      phone,
      birthday,
      gender,
      selected,
      starred,
      frequently
    }
  }

  render() {
    const {onSaveContact, onContactClose, open, contact} = this.props;
    const {id, firstname, lastname, birthday, gender, email, phone, selected, starred, frequently} = this.state;
    let {thumb} = this.state;
    if (!thumb) {
      thumb = require('assets/images/placeholder.jpg');
    }
    return (
      <Modal
        title={contact.firstname === '' ?
          'Ajouter' : 'Enregistrer'}
        toggle={onContactClose} visible={open}
        closable={false}
        onOk={() => {
          if (firstname === '')
            return;
          onContactClose();
          onSaveContact(
            {
              'id': id,
              'firstname': firstname,
              'lastname': lastname,
              'thumb': thumb,
              'email': email,
              'phone': phone,
              'birthday': birthday,
              'gender': gender,
              'selected': selected,
              'starred': starred,
              'frequently': frequently,
            });
          this.setState({
            'id': id + 1,
            'name': '',
            'thumb': '',
            'email': '',
            'phone': '',
            'birthday': '',
            'gender': '',
            'selected': '',
            'starred': '',
            'frequently': '',
          })

        }}
        onCancel={onContactClose}>

        <div className="gx-modal-box-row">
          <div className="gx-modal-box-avatar">
            <Avatar size="large" src={thumb}/>
          </div>

          <Form  {...formItemLayout}
          >
            <Form.Item
              name="ID"
              label="ID"
            >
              <Input defaultValue={id}
                     disabled={true}/>
            </Form.Item>
            <Form.Item
              name="Nom"
              label="Nom"
              rules={[
                {
                  type: 'string',
                  message: 'Non valide!',
                },
                {
                  required: true,
                  message: 'Champ obligatoire!',
                },
              ]}
            >
              <Input defaultValue={lastname}
                     placeholder="Nom"
                     onChange={(event) => this.setState({lastname: event.target.value})} />
            </Form.Item>

            <Form.Item
              name="Prénom"
              label="Prénom"
              rules={[
                {
                  type: 'string',
                  message: 'Non valide!',
                },
                {
                  required: true,
                  message: 'Champ obligatoire!',
                },
              ]}
            >
              <Input defaultValue={firstname}
                     placeholder="Prénom"
                     onChange={(event) => this.setState({firstname: event.target.value})} />
            </Form.Item>
            <Form.Item
              name="E-mail"
              label="E-mail"
              rules={[
                {
                  type: 'email',
                  message: 'Non valide!',
                },
                {
                  required: true,
                  message: 'Champ obligatoire!',
                },
              ]}
            >
              <Input defaultValue={email}
                     placeholder="E-mail"
                     onChange={(event) => this.setState({email: event.target.value})} />
            </Form.Item>
            <Form.Item
              name="Téléphone"
              label="Téléphone"
              rules={[
                {
                  type: 'phone',
                  message: 'Non valide!',
                },
                {
                  required: true,
                  message: 'Champ obligatoire!',
                },
              ]}
            >
              <Input defaultValue={phone}
                     placeholder="Téléphone"
                     onChange={(event) => this.setState({phone: event.target.value})} />
            </Form.Item>

            <Form.Item
              name="Date de naissance"
              label="Date de naissance"
              rules={[
                {
                  type: 'date',
                  message: 'Non valide!',
                },
                {
                  required: true,
                  message: 'Champ obligatoire!',
                },
              ]}
            >
              <DatePicker className="gx-mb-3 gx-w-100"
                          format={dateFormat}/>
            </Form.Item>

            <Form.Item
              name="Sexe"
              label="Sexe"
            >
              <RadioGroup  defaultValue={gender}>
                <RadioButton value="M"><ManOutlined  />M</RadioButton>
                <RadioButton value="F" ><WomanOutlined  />F</RadioButton>
              </RadioGroup>
            </Form.Item>


          </Form>
        </div>
      </Modal>
    );
  }
}

export default AddContact;
